<template>
    <div v-if="!tooSoon">
        <div v-if="reactionTime <= 150">
            <h1>You are ... not a human 😳😲</h1>
            <h2>Your Reaction Time is - {{ reactionTime }}ms </h2>
        </div>

        <div v-else-if="reactionTime > 150 && reactionTime <= 200">
            <h1>Wow Good Job...that's fast</h1>
            <h2>Your Reaction Time is - {{ reactionTime }}ms </h2>
        </div>

        <div v-else-if="reactionTime > 200 && reactionTime < 300">
            <h1>Good Job...that's human average</h1>
            <h2>Your Reaction Time is - {{ reactionTime }}ms </h2>
        </div>

        <div v-else-if="reactionTime > 300 && reactionTime < 400">
            <h1>Not Bad...Not Bad ( Boomer )</h1>
            <h2>Your Reaction Time is - {{ reactionTime }}ms </h2>
        </div>

        <div v-else-if="reactionTime > 400 && reactionTime < 500">
            <h1>Sad situation...you might need some sleep</h1>
            <h2>Your Reaction Time is - {{ reactionTime }}ms </h2>
        </div>

        <div v-else-if="reactionTime > 500 && reactionTime < 600">
            <h1>Ughh...maybe wake up?</h1>
            <h2>Your Reaction Time is - {{ reactionTime }}ms </h2>
        </div>

        <div v-else-if="reactionTime > 600 && reactionTime <= 1000">
            <h1>Get some help</h1>
            <h2>Your Reaction Time is - {{ reactionTime }}ms </h2>
        </div>

        <div v-else-if="reactionTime > 1000">
            <h1>Are you even alive 💀</h1>
            <h2>Your Reaction Time is - {{ reactionTime }}ms </h2>
        </div>
    </div>

    <div v-else>
        <h1>Too Soon...</h1>
    </div>
</template>

<script>
    export default {
        props: ['reactionTime', 'tooSoon']
    }
</script>