<template>
    <h1>{{ title }}</h1>
    <button id="play" :class="{ disabled: isPlaying }" @click="startGame">{{ playButtonText }}</button>
    <Result v-if="showResult" :reactionTime="reactionTime" :tooSoon="tooSoon"/>
    <Block v-if="isPlaying && showBlock" @closeBlock="hideBlock"/>
    <div v-else-if="isPlaying && !showBlock" id="backgroundDiv" @click="hideBlock"></div>
    
</template>

<script>
    import Block from "./components/Block";
    import Result from "./components/Result";

    export default {
        data() {
            return {
                title: 'Reaction Timer',
                playButtonText: 'Play',
                isPlaying: false,
                delay: null,
                showBlock: false,
                reactionTime: -30,
                showResult: false,
                tooSoon: false,
                timeOut: null,
            }
        },

        components: {
            Block,
            Result
        },

        methods: {
            startGame() {
                if(this.isPlaying){
                    return
                }
                this.isPlaying = true;
                this.showResult = false,
                this.tooSoon = false,
                this.delay = (Math.floor(Math.random() * 3) + 2) * 1000
                this.timeOut = setTimeout(() => {
                    this.showBlock = true;
                }, this.delay)
            },

            hideBlock(reactionTime = -30) {
                if(!this.showBlock){
                    this.tooSoon = true;
                    clearTimeout(this.timeOut)
                }

                this.showBlock = false;
                this.isPlaying = false;
                this.reactionTime = reactionTime;
                this.showResult = true;
                console.log(this.reactionTime)
            },

        }
    }
</script>

<style>
    #app{
        font-family: Avenir, Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2c3e50;
    }

    #app h1{
        font-size: 3vw;
    }

    #play{
        border: 0.2vw solid green;
        background-color: rgb(91, 131, 31);
        font-size: 1.5vw;
        padding: 0.5vw 1vw;
        color: white;
    }

    #play.disabled{
        background-color: grey !important;
        border: 0.2vw solid black;
    }

    #backgroundDiv{
        width: 100vw;
        height: 30vw;
        left: 0;
        position: absolute;
    }
</style>