<template>
    <div id="block" @click="closeBlock">
        {{ title }} 
    </div>
</template>

<script>
    export default {
        data(){
            return {
                title: 'Click Me',
                timer: null,
                reactionTime: null,
            }
        },

        methods: {
            closeBlock() {
                clearInterval(this.timer);
                this.$emit('closeBlock', this.reactionTime);
            }
        },
        
        mounted() {
            this.reactionTime = -30;
            this.timer = setInterval(() => {
                this.reactionTime += 5
            }, 5)
        }
    }
</script>

<style>
    #block{
        width: 40vw;
        padding: 10vw 0;
        border-radius: 1vw;
        background-color: rgb(41, 156, 108);
        text-align: center;
        font-size: 4vw;
        color: white;
        margin: auto;
        margin-top: 3vw;
    }
</style>